    draw(data) {
      let legendData = [];
      let xAxisData = [];
      let seriesData = [];
      let maxValue = 0;
      if (data.length == 0) {
        xAxisData = {
          data: [],
          type: "time",
          splitLine: {
            show: false
          }
        };
      }
      for (let i in data) {
        legendData.push(i);
        let xAxisTmp = [];
        let seriesTmp = [];
        for (let j in data[i]) {
          if (data[i][j] > maxValue) {
            maxValue = data[i][j];
          }
          var itemDate = new Date(j - 1);
          xAxisTmp.push(itemDate.getMonth() + 1 + "/" + itemDate.getFullYear());
          seriesTmp.push({
            name: itemDate.toString(),
            value: [
              [
                itemDate.getFullYear(),
                itemDate.getMonth() + 1,
                itemDate.getDate()
              ].join("/"),
              data[i][j]
            ]
          });
        }
        xAxisData.push({
          data: xAxisTmp,
          type: "time",
          splitLine: {
            show: false
          }
        });
        seriesData.push({
          name: i,
          type: "line",
          data: seriesTmp,
          areaStyle: { normal: {} }
        });
      }
